#navbar-menu {
  opacity: 1;
  position: fixed;
}

#background-img {
  width: 100%;
  height: max-content;
}

#welcome {
  background-image: url('../public/assets/La-Atitlan.jpg');
  position: fixed;
}

.answer {
  background-color: lightgray;
  border-radius: 5px;
  padding: 1rem 0px 1rem 2.5%;
}

.calification {
  display: flex;
}

.calification-star {
  width: 20px;
}

.card {
  width: 100%;
  padding: 2rem;
  box-shadow: 1px 1px gray, -1px -1px gray, -1px 1px gray, 1px -1px gray;
  border: solid gray 1px;
}

.card:hover {
  box-shadow: 5px 5px lightgray, -5px -5px lightgray, -5px 5px lightgray, 5px -5px lightgray;
  cursor: pointer;
}

.card:hover > img {
  transform: scale(1.05);
}

.card-img-text {
  position: fixed;
  color: white;
  font-size: 20pt;
  display: none;
}

.card-img-top {
  border-radius: 10px;
  transition: 0.5s;
  object-fit: cover;
  overflow: hidden;
}

.card-img-top:hover {
  transform: scale(1.03);
}

.connection-text {
  text-align: center;
  border: 1px solid #063970;
  font-family: Helvetica, "Helvetica Neue", Arial, sans-serif;
}

.container {
  text-align: center;
  padding: 5% 30% 5% 30%;
}

.dropdown {
  position: relative;
}

.img-non-selected {
  filter: brightness(60%);
}

.card-text {
  text-align: justify;
}

.logo {
  position: fixed;
  width: 25rem;
  align-self: center;
}

.nav-link{
  color: black;
}

.phone-version {
  display: none;
}

.price-col {
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.review {
  border: 5rem 0pc 5rem 0pc;
}

.standard-version {
  display: flex;
}

.tour {
  padding: 2.5% 10% 2.5% 10%;
}

.tour-container {
  padding: 5%;
}

.tour-image {
  display: flex;
  justify-content: left;
  align-items: flex-end;
}

.tour-img-gallery {
  display: block;
}

.tour-img-gallery img {
  margin: .25rem;
  border-radius: 5px;
  width: 100%;
}

.tour-img-main {
  align-items: center;
  display: flex;
}

.tour-main-image {
  width: 100%;
  border-radius: 5px;
  box-shadow: 1px 1px gray;
}

.tour-price-container {
  background-color: gray;
  color: white;
  padding: 1rem;
  border-radius: 5px;
}

.tour-title {
  display: block;
}

.tours-from {
  width: 100%;
}

.tours-from-img {
  width: 22rem;
}

.tours-from div {
  position: relative;
  text-align: center;
}

.tours-from p {
  text-align: center;
  color: white;
  font-size: 2pc;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

p {
  text-align: justify;
}

@media (max-width: 1170px) {
  .phone-version {
    display: block;
    padding-left: 0px;
    padding-right: 0px;
    border-left: 0px;
    border-right: 0px;
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 1pc;
  }
  .standard-version {
    display: none;
  }
  .tour-img-gallery {
    display: flex;
  }
  .tour-title {
    background-color: white;
  }
  .tour-img-main {
    width: 100%;
  }

  .tour-price-container {
    border-radius: 5px;
  }
}
